a{
    text-decoration: none;
    color: inherit;
}
ul{
    list-style-type: none;
    padding: 0;
}
.menu{
    padding: 0;
    list-style-type: none;
    display: flex;
    gap: 10px;
}
.menu>li{
    position: relative;
}
menu>li:hover>ul{
visibility: visible;
}
.menu ul{
    position: absolute;
    z-index: 1;
    background-color: white;
    padding: 10px;
    box-shadow: 0 1px 5px 0 #0004;
    visibility: hidden;
}